.date-picker-field__label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.25rem;
}

.date-picker-field__required {
  color: #ef4444;
  margin-left: 0.25rem;
}

.date-picker-field__help {
  font-size: 0.875rem;
  color: #6b7280;
  margin-bottom: 0.5rem;
}

.date-picker-field__input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  outline: none;
  transition: border-color 0.15s, box-shadow 0.15s;
}

.date-picker-field__input:focus {
  outline: none;
  box-shadow: 0 0 0 2px #3b82f6;
  border-color: #3b82f6;
}

.date-picker-field__input--error {
  border-color: #ef4444;
}

.date-picker-field__input--error:focus {
  box-shadow: 0 0 0 2px #ef4444;
  border-color: #ef4444;
}

.date-picker-field__error {
  margin-top: 0.25rem;
  font-size: 0.875rem;
  color: #dc2626;
}

.date-picker-field__display {
  color: #1d2327;
}

.date-picker-field__display--empty {
  color: #9ca3af;
  font-style: italic;
}

.date-picker-field__display--invalid {
  color: #dc2626;
  font-style: italic;
}
