.sortable-children-field__label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
}

.sortable-children-field__help {
  font-size: 0.875rem;
  color: #6b7280;
  margin-top: -0.25rem;
}

.sortable-children-field__placeholder {
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  padding: 1rem;
  background-color: #f9fafb;
}

.sortable-children-field__message {
  font-size: 0.875rem;
  color: #6b7280;
}

.sortable-children-field__error-container {
  border: 1px solid #fecaca;
  border-radius: 0.375rem;
  padding: 1rem;
  background-color: #fef2f2;
}

.sortable-children-field__error-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: #dc2626;
  margin-bottom: 0.5rem;
}

.sortable-children-field__error-message {
  font-size: 0.875rem;
  color: #dc2626;
}

.sortable-children-field__empty {
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  padding: 1rem;
  background-color: #f9fafb;
}

.sortable-children-field__container {
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  overflow: hidden;
  background-color: white;
}

.sortable-children-field__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}

.sortable-children-field__count {
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
}

.sortable-children-field__actions {
  display: flex;
  gap: 0.5rem;
}

.sortable-children-field__button {
  padding: 0.25rem 0.75rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  border: 1px solid;
  cursor: pointer;
  transition: background-color 0.15s;
}

.sortable-children-field__button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.sortable-children-field__button--cancel {
  background-color: #e5e7eb;
  color: #374151;
  border-color: #d1d5db;
}

.sortable-children-field__button--cancel:hover:not(:disabled) {
  background-color: #d1d5db;
}

.sortable-children-field__button--save {
  background-color: #2563eb;
  color: white;
  border-color: #2563eb;
}

.sortable-children-field__button--save:hover:not(:disabled) {
  background-color: #1d4ed8;
}

.sortable-children-field__button--retry {
  margin-top: 0.75rem;
  padding: 0.375rem 0.75rem;
  background-color: #dc2626;
  color: white;
  border-color: #dc2626;
}

.sortable-children-field__button--retry:hover {
  background-color: #b91c1c;
}

.sortable-children-field__list {
  border-top: 1px solid #e5e7eb;
}

.sortable-children-field__list > *:not(:last-child) {
  border-bottom: 1px solid #e5e7eb;
}

.sortable-children-field__item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  cursor: move;
  transition: opacity 0.15s, background-color 0.15s;
}

.sortable-children-field__item:hover {
  background-color: #f9fafb;
}

.sortable-children-field__item--dragging {
  opacity: 0.5;
}

.sortable-children-field__drag-handle {
  color: #9ca3af;
}

.sortable-children-field__drag-icon {
  width: 1.25rem;
  height: 1.25rem;
}

.sortable-children-field__item-content {
  flex: 1;
}

.sortable-children-field__item-title {
  font-size: 0.875rem;
  font-weight: 500;
  color: #111827;
}

.sortable-children-field__item-position {
  font-size: 0.75rem;
  color: #6b7280;
  font-family: monospace;
}

.sortable-children-field__warning {
  background-color: #fef3c7;
  border: 1px solid #fde68a;
  border-radius: 0.375rem;
  padding: 0.75rem;
}

.sortable-children-field__warning-text {
  font-size: 0.875rem;
  color: #92400e;
}

.sortable-children-field__display {
  color: #9ca3af;
  font-style: italic;
}
